                      </g>
                    </g>
                  </svg>
                </button>
              </div>
              <img :src="item.image" alt="" />
              <div class="content-info">
                <div class="content-info-name">動物品種 : {{ item.title }}</div>
                <div class="content-info-category">
                  類型 : {{ item.category }}
                </div>
                <div class="content-info-gender">性別 : {{ item.unit }}</div>
                <div class="content-info-more">
                  <a
                    href=""
                    class="color-main adopt-more"
                    @click.prevent="learnmore(item.id)"
                    >了解更多</a
                  >
                  <a
                    href=""
                    class="color-main adopt-more ml-2"
                    @click.prevent="addCart(item.id)"
                    >預約領養</a
                  >
                </div>
              </div>
            </div>
          </div>
        </div>
        <page
          :pages="pagination"
          @emitPage="products"
          class="d-flex m-auto mt-3 justify-content-center"
        ></page>
      </div>
    </div>
  </div>
</template>
<script>
import page from '@/components/pagination'

export default {
  name: 'adopt',
  components: {
    page
  },
  data () {
    return {
      data: [],
      newData: [],
      adopt: [],
      category: 'all',
      newCategory: new Set(),
      // 新增loaclstorage
      favorite: JSON.parse(localStorage.getItem('addfav')) || [],
      favoriteadd: false,
      adoptVal: 0,
      pagination: [],
      isLoading: false
    }
  },
  methods: {
    products: function (page = 1) {
      // 取得產品
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/products?page=${page}`
      const vm = this
      vm.isLoading = true
      vm.$http.get(url).then(function (res) {
        vm.isLoading = false
        vm.data = res.data.products
        vm.pagination = res.data.pagination
      })
    },
    categorylist: function (list) {
      // 列表取得與產品過濾
      const vm = this
      vm.category = list
      vm.products()
    },
    editfav: function (item) {
      // 新增最愛
      const vm = this
      const num = vm.favorite.indexOf(item.id)
      if (num === -1) {
        vm.favorite.push(item.id)
        vm.isLoading = true
        vm.favoriteadd = true
      } else {
        vm.favorite.splice(num, 1)
        vm.favoriteadd = false
      }
      localStorage.setItem('addfav', JSON.stringify(vm.favorite))
      vm.isLoading = false
      vm.$bus.$emit('favnum', vm.favorite.length)
    },
    learnmore: function (id) {
      // 取得單一品項
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`
      const vm = this
      vm.$http.get(url).then(function (res) {
        vm.adopt = res.data.product.id
        vm.$router.push(`/adopt/${res.data.product.id}`)
      })
    },
    addCart: function (id, qty = 1) {
      // 預約領養
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      const vm = this
      vm.isLoading = true
      const cart = {
        product_id: id,
        qty: qty
      }
      vm.$http.post(url, { data: cart }).then(function (res) {
        if (res.data.success) {
          vm.getCart()
          vm.isLoading = false
        } else {
          console.log('error')
          vm.isLoading = false
        }
      })
    },
    getCart: function () {
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      const vm = this
      vm.isLoading = true
      vm.$http.get(url).then(function (res) {
        vm.isLoading = false
        vm.adoptVal = res.data.data.carts.length
        vm.$bus.$emit('cartVal', vm.adoptVal)
      })
    }
  },
  computed: {
    filterData: function () {
      // 產品過濾
      const vm = this
      if (vm.category !== 'all') {
        const filteradopt = vm.data.filter(function (item) {
          return vm.category === item.category
        })
        vm.data = filteradopt
      }
      return vm.data
    },
    categoryitem: function () {
      // 列表取得
      const vm = this
      vm.data.forEach(function (item) {
        vm.newCategory.add(item.category)
      })
      return vm.newCategory
    }
  },
  created () {
    const vm = this
    vm.products()
    vm.getCart()
  }
}
</script>
